.sidebar-link {
    padding: 10px 20px; /* Adjust padding to create space */
    background-color: #007bff;
    color: #fff;
    text-decoration: none;
    border: 2px solid rebeccapurple; /* Add border to the links */
    border-radius: 5px;
    margin: 10px;
    min-width: 200px;
    display: inline-block; /* Ensure each link is a block-level element */
    line-height: 1; /* Ensure consistent line height */
  }
  
  .sidebar-link:hover {
    background-color: #007bffd2;
  }
  
  .sidebar {
    display: flex;
    width: 100%;
    flex-direction:start;
    background-color: rgba(18, 18, 199, 0.281);
    align-items: center;
  }
  .sidebarClick{
    display: none;
  }
  @media (max-width: 768px) {
    .sidebar{display: none;}
    .sidebarClick {
      display: flex;
      background-color: rgba(0, 0, 0, 0.219);
      flex-direction: column; /* Vertical when screen size is small */
      flex: 1;
      max-width: 100%;
     align-items: flex-start;
    }
    .sidebar-link {
        max-width: 100%;
        min-width: 100px;
        padding: auto;
    }
  }
  