
.header {
  display: flex;
  align-items: center;
  height: 120px;
  background-color: #add8e6;
  justify-content: space-between;
}
.header > h1 {
  margin: 0px 40px;
  font-size: 50px;
  font-weight: bold;
  color: #333333;
}
.userIcon {
  color: #000000;
  padding-right: 50px;
  font-size: 40px;
}
.userIcon:hover {
  color: #0cb449;
}
.container {
  width: 100%;
}







.morningEntries {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  border: 2px solid;
  border-collapse: collapse;
  width: 100%;
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.MorningHeading {
  padding: 5px;
  background-color: rgb(240, 158, 219);
  color: white;
  border-bottom: 1px black solid;
}
.MorningData td{
      text-align: center;
      vertical-align: middle;
}
.MorningData:hover {
  background-color: aquamarine;
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.MorningHeading:hover {
  box-shadow: 5px 5px 8px rgb(126, 123, 123);
}
.green-row {
  background-color: green;
}
.EntriesRow{
  display: flex;
  width: 100%;
}
.DateSelector{
  display: inline-flex;
  border: 2px solid;
  border-radius: 8px;
  background-color: rgba(117, 33, 196, 0.493);
  justify-content: center;
  align-items: center;
}
.DateSelector input{
  border-radius: 7px;
  margin: 2px;
  padding: 3px;
  background-color: rgba(255, 255, 255, 0.678);
}
.DateSelector button{
  height: 30px;
  margin-left: 8px;
  margin-top: 2px;
  color: white;
  background-color: rgb(145, 38, 207);
  width: 50px;

}
.main {
  width: 100%;
}
.container1 {
  display: flex;
  width: 100%;
  flex-direction: column;
}
























@media (max-width: 768px) {
  .full{
    display: grid;
    grid-template-columns: 1fr 5fr;
  }
}